import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {Subject, timer} from 'rxjs';
import {takeUntil} from 'rxjs/operators';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';
import {coreUtils} from 'src/app/core/util/coreUtils';

import {chartVariables} from '../chart-variables';

@Component({
  selector: 'app-monitor-charts',
  templateUrl: './monitor-charts.component.html',
  styleUrls: ['./monitor-charts.component.scss']
})
export class MonitorChartsComponent implements OnInit, OnDestroy {
  chartData: any = {};
  chartOptions: any = {};
  chart_type = '';

  private killRefershCharts: Subject < void > = new Subject();

  constructor(
    private dataService: DataService) {}

  @Input()
  set chartInput(data: any) {
    if (data) {
      this.chartData = {};
      this.chartData = data;
      if (data.refresh === 'auto') {
        timer(0, 30000)
          .pipe(
            // This kills the request if the user closes the component
            takeUntil(this.killRefershCharts),
          )
          .subscribe(t => {
            this.getChartData(data.fromDate, data.toDate);
          });
      } else {
        this.getChartData(data.fromDate, data.toDate);
      }
    }
  }

  @Input()
  set manualRefresh(val: any) {
    if (val) {
      this.getChartData(this.chartData.fromDate, this.chartData.toDate);
    }
  }

  getChartData(fDate, tDate) {
    const obj = {
      fromDate: fDate,
      toDate: tDate,
      tenant_id: coreUtils.removeHifen(window.location.hostname),
      header: this.chartData.header,
      flip_side: this.chartData.flipSide ? this.chartData.flipSide : null
    };
    this.dataService.getEachCardDetails(obj, this.chartData.route)
      .subscribe(
        resp => {
          if(resp) {
            this.processChart(resp);
          }
        },
        (error) => {});
  }

  processChart(respData ? : any) {
    this.chart_type = this.chartData.chart_type ? this.chartData.chart_type :
                        (respData.chart_type ? respData.chart_type : null)
    switch (this.chart_type) {
      case 'column':
        this.chartOptions = chartVariables.COLUMN;
        this.chartOptions.xAxis[0].data = respData.axiscolumns;
        this.chartOptions.series[0].name = respData.barname;
        this.chartOptions.series[0].data = this.formBarData(respData.axisvalues);
        this.chartOptions.heading = respData.heading;
        //this.chartOptions.series[0].itemStyle.color = this.pickRandomColor();
        this.setHeightWidth();
        break;
      case 'stacked_column':
        this.chartOptions = chartVariables.STACKED_COLUMN;
        this.chartOptions.xAxis[0].data = respData.axiscolumns;
        this.chartOptions.legend.data = this.formLegend(respData);
        this.chartOptions.series = this.formSeries(respData);
        this.chartOptions.heading = respData.heading;
        this.setHeightWidth();
        break;
      case 'bar':
        this.chartOptions = chartVariables.BAR;
        this.chartOptions.yAxis[0].data = respData.axiscolumns;
        this.chartOptions.series[0].name = respData.barname;
        this.chartOptions.series[0].data = respData.axisvalues;
        this.chartOptions.heading = respData.heading;
        this.setHeightWidth();
        break;
      case 'stacked_bar':
        this.chartOptions = chartVariables.STACKED_BAR;
        this.chartOptions.yAxis[0].data = respData.axiscolumns;
        this.chartOptions.legend.data = this.formLegend(respData);
        this.chartOptions.series = this.formSeries(respData);
        this.chartOptions.heading = respData.heading;
        this.setHeightWidth();
        break;
      case 'pie':
        this.chartOptions = chartVariables.PIE;
        this.chartOptions.title.text = respData.heading
        this.chartOptions.title.subtext = respData.subheading ? respData.subheading : ''
        this.chartOptions.series[0].name = respData.barname;
        this.chartOptions.series[0].data = this.formPieData(respData.stackedcolumns);
        this.chartOptions.heading = respData.heading;
        this.setHeightWidth();
        break;
      case 'line':
        this.chartOptions = chartVariables.LINE;
        this.chartOptions.xAxis[0].data = respData.axiscolumns;
        this.chartOptions.series[0].data = respData.axisvalues;
        this.chartOptions.series[0].areaStyle.color = respData.color;
        this.chartOptions.series[0].lineStyle.color = respData.color;
        this.chartOptions.heading = respData.heading;
        this.setHeightWidth();
        break;
      default:
        break;
    }
  }

  formBarData(arrayvalues: []) {
    let data = [];
    if(arrayvalues && arrayvalues.length > 0) {
      arrayvalues.forEach((barValue, index) => {
        data.push({
          value: barValue,
          itemStyle:{color: this.pickColorByIndex(index)} 
        })
      })
    }
    return data;
  }

  pickColorByIndex(index) {
    const items = ['#37A2DA','#FFDB5C','#FF9F7F','#FB7293','#E062AE','#8378EA','#9FE6B8','#E7BCF3']
    return items[index];
  }

  formPieData(response) {
    let data = [];
    if(response.stackedcolumns) {
      Object.keys(response.stackedcolumns).forEach(stack => {
        data.push({
          name: stack,
          value: response.stackedcolumns[stack]
        })
      })
    }
    return data;
  }

  formLegend(response) {
    if(response.stackedcolumns) {
      return Object.keys(response.stackedcolumns)
    }
    else {
      return []
    }
  }

  formSeries(response) {
    let series = [];
    if(response.stackedcolumns) {
      Object.keys(response.stackedcolumns).forEach(stack => {
        series.push({
          name: stack,
          type: 'bar',
          stack: 'total',
          label: {
            normal: {
              show: true,
              position: 'insideRight'
            }
          },
          data: response.stackedcolumns[stack]
        })
      })
    }
    return series;
  }

  setHeightWidth() {
    this.chartOptions.height = this.chartData.chartHeight - 140;
    this.chartOptions.width = this.chartData.chartWidth - 40;
  }

  ngOnInit() {}

  checkIfAllEmpty(obj: any) {
    return coreUtils.isEmpty(obj)
  }

  onError(event: any) {
    event.message = '';
  }

  ngOnDestroy() {
    this.killRefershCharts.next();
  }
}
