import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-paginator',
  templateUrl: './paginator.component.html',
  styleUrls: ['./paginator.component.scss']
})
export class PaginatorComponent implements OnInit {
  paginatorData: any = {};
  numberofRecordsPerPage = 20;
  firstPage = 1;
  @Output() nextorPreviousRecords: EventEmitter<any> = new EventEmitter<any>();
  constructor() {}

  ngOnInit() {}

  @Input()
  set paginationData(data: any) {
    this.paginatorData = data;
  }

  nextRecords() {
    if (this.paginatorData.end < this.paginatorData.total) {
      this.nextorPreviousRecords.emit({
        start: this.paginatorData.end + 1,
        end: this.paginatorData.end + this.numberofRecordsPerPage,
        pageNumber: this.getNextPageNumber()
      });
    }
  }

  previousRecords() {
    if (this.paginatorData.start > 1) {
      this.nextorPreviousRecords.emit({
        start: this.paginatorData.start - this.numberofRecordsPerPage,
        end: this.paginatorData.start - 1,
        pageNumber: this.getPreviousPageNumber()
      });
    }
  }

  gotoFirstPage() {
    this.nextorPreviousRecords.emit({
      start: this.firstPage,
      end: this.numberofRecordsPerPage,
      pageNumber: 1
    });
  }

  gotoLastPage() {
    this.nextorPreviousRecords.emit({
      start: this.paginatorData.total - this.numberofRecordsPerPage,
      end: this.paginatorData.total,
      pageNumber: 0
    });
  }

  getPreviousPageNumber() {
    if (this.paginatorData.start - this.numberofRecordsPerPage === 1) {
      return 1;
    }
    return 0;
  }

  getNextPageNumber() {
    return 0;
  }

  checkIfFirstPage() {
    return this.paginatorData.start === 1 ? true : false;
  }

  checkIfLastPage() {
    return this.paginatorData.end === this.paginatorData.total ? true : false;
  }
}
