import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {DomSanitizer} from '@angular/platform-browser';
import {MaskEventService} from 'src/app/core/services/mask-event.service';
import {PdfHandlerService} from 'src/app/field-exception/service/pdfhandler/pdf-handler.service';

export interface ScanDialogData {
  case_id: string;
  buttons: [];
}

@Component({
  selector: 'app-scan-file-view',
  templateUrl: './scan-file-view.component.html',
  styleUrls: ['./scan-file-view.component.scss']
})
export class ScanFileViewComponent implements OnInit {
  convImgFromPDF = [];
  file_name = '';
  pdfSrc:any = {};
  pdfUrl: any;
  buttons = [];
  imageWidth = 100;
  pdfError = 0;
  disablebutton = false;

  constructor(
      private pdfHandlerService: PdfHandlerService,
      private sanitizer: DomSanitizer, private maskEvent: MaskEventService,
      public dialogRef: MatDialogRef<ScanFileViewComponent>,
      @Inject(MAT_DIALOG_DATA) public data: ScanDialogData) {}

  ngOnInit() {
    // this.dialogRef.updatePosition({top: '20'})

    this.file_name = this.data.case_id;
    this.buttons = this.data.buttons;

    this.pdfSrc = {type: 'PDF', name: this.file_name + '.pdf', pages: []};

    this.pdfUrl = this.sanitizer.bypassSecurityTrustResourceUrl(
        'assets/pdf/' + this.file_name + '.pdf');

    // this.convertPdfToImage();
  }

  convertPdfToImage() {
    try {
      const url = '../assets/pdf/' + this.pdfSrc.name;
      this.pdfHandlerService.setPdfDocObjects(this.pdfSrc, url)
          .then((pdf: any) => {
            const totalPages = pdf.pdfInfo.numPages;
            const go = async () => {
              for (let i = 1; i <= totalPages; i++) {
                const pageNumber = i;
                const page = await pdf.getPage(pageNumber);
                const scale = 2;
                const viewport = page.getViewport(scale);
                const canvas: any = document.createElement('canvas');
                const context = canvas.getContext('2d');
                canvas.height += viewport.height;
                canvas.width = viewport.width;
                const renderContext = {canvasContext: context, viewport};
                const task = page.render({canvasContext: context, viewport});
                await task.promise;
                const file = canvas.toDataURL('image/jpeg');
                this.convImgFromPDF.push(file);
                this.pdfError = this.convImgFromPDF.length > 0 ? 0 : 1;
              }
            };
            go();
          });
    } catch (error) {
      this.maskEvent.unMask('maskApp');
      this.pdfError = 1;
    }
  }

  onBtnClick(val: any, filename?: any): void {
    this.dialogRef.close({close: val, name: filename});
  }
}
