import {computeStyle} from '@angular/animations/browser/src/util';
import {Component, Input, OnInit} from '@angular/core';
import {MatTableDataSource} from '@angular/material';
import {ExceptionTableModel} from 'src/app/core/model/exceptions/exception-table-model';
import {MaskEventService} from 'src/app/core/services/mask-event.service';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';
import {StatusService} from 'src/app/core/services/statusmessage/status.service';
import {coreUtils} from 'src/app/core/util/coreUtils';

@Component({
  selector: 'app-form-excel-tab-table',
  templateUrl: './form-excel-tab-table.component.html',
  styleUrls: ['./form-excel-tab-table.component.scss']
})
export class FormExcelTabTableComponent implements OnInit {
  tableConfig: any;
  displayedColumns = [];
  searchHeaderColumns = [];
  activeTab: string;
  excelTypeDataSource = new MatTableDataSource();

  @Input()
  set tableData(data: any) {
    this.tableConfig = data;
    this.formTable();
  }

  @Input()
  set tab(value: string) {
    this.activeTab = value;
  }

  constructor(
      private dataService: DataService, private maskEvent: MaskEventService,
      private statusService: StatusService) {}

  ngOnInit() {}

  applyFilter(filterValue: string) {
    this.excelTypeDataSource.filter = filterValue.trim().toLowerCase();
  }

  formTable() {
    this.searchHeaderColumns = [];
    this.displayedColumns = this.tableConfig.column;
    this.displayedColumns.forEach(column => {
      this.searchHeaderColumns.push('s' + column);
    });
    this.excelTypeDataSource =
        new MatTableDataSource<ExceptionTableModel>(this.tableConfig.data);
  }

  searchDatabase(value: string, columnName: string) {
    const params = {
      column_name: [columnName.substring(1)],
      tab: this.activeTab,
      search_string: value,
      tenant_id: coreUtils.removeHifen(window.location.hostname)
    };
    this.maskEvent.mask('maskApp', 'Loading Excel Data ..');
    this.dataService.getExcelData(params).subscribe(
        resp => {
          this.maskEvent.unMask('maskApp');
          if (resp.flag && resp.data && resp.data.length > 0) {
            this.excelTypeDataSource =
                new MatTableDataSource<ExceptionTableModel>(resp.data);
          } else {
            this.statusService.post('info', 'No Records Found', 10);
          }
        },
        () => {
          this.maskEvent.unMask('maskApp');
        });
  }
}
