import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {MatDialog, MatDialogConfig, MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import {ActivatedRoute, Router} from '@angular/router';
import {Subject, timer} from 'rxjs';
import {takeUntil} from 'rxjs/operators';
import {ExceptionTableModel} from 'src/app/core/model/exceptions/exception-table-model';
import {AuthService} from 'src/app/core/services/global/authentication.service';
import {MaskEventService} from 'src/app/core/services/mask-event.service';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';
import {StatusService} from 'src/app/core/services/statusmessage/status.service';
import {CallbackPopupComponent} from 'src/app/field-exception/callback-popup/callback-popup.component';
import { coreUtils } from 'src/app/core/util/coreUtils';

@Component({
  selector: 'app-exception-list',
  templateUrl: './exception-list.component.html',
  styleUrls: ['./exception-list.component.scss']
})

export class ExceptionListComponent implements OnInit, OnDestroy {
  displayedColumns = [];
  columnMap:any = {};
  isEditdisabled = true;
  isDeleteDisabled = true;
  dataSource = new MatTableDataSource();
  pathId = '1';
  forceTempDD = [];
  @ViewChild(MatSort) sort: MatSort;
  private killTrigger: Subject<void> = new Subject();
  getExceptionRespReceived = false;
  firstCall = true;
  paginatorData = {};
  currentPageNumber = 1;
  numberofRecordsPerPage = 20;

  constructor(
      private dataService: DataService, private maskEvent: MaskEventService,
      private router: Router, private route: ActivatedRoute,
      private authService: AuthService, private dialog: MatDialog,
      private statusService: StatusService) {}
  templateexceptionobj = {exception_type: 'Template'};

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  ngOnInit() {
    this.maskEvent.mask('maskApp', 'Loading ..');
    this.pathId = this.route.snapshot.queryParams.pathId;
    timer(0, 30000)
        .pipe(
            // This kills the request if the user closes the component
            takeUntil(this.killTrigger),
            )
        .subscribe(t => {
          if ((this.getExceptionRespReceived || this.firstCall) &&
              this.currentPageNumber === 1) {
            this.getExceptionsList(1, this.numberofRecordsPerPage);
          }
        });
  }

  checkEditPermission(element: any) {
    return (
        element.operator ?
            (element.operator.toString().length > 0 ? false : true) :
            true);
  }

  getExceptionsList(start, end) {
    this.firstCall = false;
    this.getExceptionRespReceived = false;
    const params = {queue_id: this.pathId, start, end};
    this.dataService.getExceptions(params).subscribe(
        (response) => {
          this.getExceptionRespReceived = true;
          this.maskEvent.unMask('maskApp');
          if (response.data && response.data.columns &&
              response.data.columns.length > 0) {
              this.columnMap = response.data.column_mapping;
              if(!coreUtils.isEmpty(this.columnMap)) {
                this.displayedColumns = [];
                Object.keys(this.columnMap).forEach(element => {
                  this.displayedColumns.push(element)
                });
              };
            this.dataSource = new MatTableDataSource<ExceptionTableModel>(
                response.data.files);
            this.forceTempDD = response.data.template_dropdown ?
                response.data.template_dropdown :
                '';
            this.paginatorData = response.data.pagination;
            this.dataSource.sort = this.sort;
            setTimeout(() => {
              this.dataSource.sort = this.sort;
            }, (1000));
          }
        },
        (error) => {
          this.maskEvent.unMask('maskApp');
        });
  }

  getValue(key) {
    return this.columnMap[key];
  }

  trainButtonClick() {
    this.router.navigate(['exceptiontrain'], {relativeTo: this.route});
  }

  onAddClick() {
    this.router.navigate(['exceptionform'], {relativeTo: this.route});
  }

  forceTemplateChange(item: any) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '300px';
    dialogConfig.data = {
      heading: 'Confirm',
      text: 'Are you sure you want to extract with this template?',
    };

    const dialogRef = this.dialog.open(CallbackPopupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const parms = {
          case_id: item.case_id,
          template_name: item.force_template
        };
        this.dataService.forceTemplate(parms).subscribe(
            (response) => {
              this.maskEvent.unMask('maskApp');
              if (response.flag) {
                this.statusService.post('info', response.message);
                this.getExceptionsList(1, this.numberofRecordsPerPage);
              }
            },
            (error) => {
              this.maskEvent.unMask('maskApp');
            });
      }
    });
  }

  getNextOrPreviousRecords(event) {
    this.currentPageNumber = event.pageNumber;
    this.getExceptionRespReceived = true;
    this.maskEvent.mask('maskApp', 'Loading ..');
    this.getExceptionsList(event.start, event.end);
  }

  gotoForm(item: any) {
    if (this.checkEditPermission(item)) {
      const routePath =
          this.authService.getNavtabItem(parseInt(this.pathId, 10))[0].path;
      this.router.navigate([routePath + 'form'], {
        queryParams: {
          pathId: this.pathId,
          caseId: item.case_id,
          file_name: item.file_name
        },
        relativeTo: this.route
      });
    }
  }

  cluster() {
    this.maskEvent.mask('maskApp', 'Loading ..');
    this.dataService.cluster().subscribe(
        (response) => {
          this.maskEvent.unMask('maskApp');
          if (response.flag) {
            this.statusService.post('success', response.message);
          } else {
            this.statusService.post('error', response.message);
          }
        },
        (error) => {
          this.maskEvent.unMask('maskApp');
        });
  }

  ngOnDestroy() {
    this.killTrigger.next();
  }
}
