import {FormField} from './form-field';
import { Validators } from '@angular/forms';
import { customNumberValidators } from 'src/app/shared/validator/customNumberValidator';

/* tslint:disable:no-any */
export class NumberFormField extends FormField {
  getType(): string {
    return 'number';
  }

  readValue(val: any): number|string {
    if (val === '' || val === undefined || val === null) {
      return '';
    } else {
      return Number(val);
    }
  }

  setLimits() {
    if (this.fieldDef.min_length) {
      this.min = Number(this.fieldDef.min_length);
    }
    if (this.fieldDef.max_length) {
      this.max = Number(this.fieldDef.max_length);
    }
    this.min = this.min || 0;
    this.max = this.max || (Number.MAX_SAFE_INTEGER);
  }

  setValidations() {
    this.validators = [];
    if (this.fieldDef.mandatory) {
      this.validators.push(Validators.required);
      if(this.fieldDef.min_length) {
        this.validators.push(customNumberValidators.min(Number(this.fieldDef.min_length)));
      }
      if(this.fieldDef.max_length) {
        this.validators.push(customNumberValidators.max(Number(this.fieldDef.max_length)));
      }
      this.required = true;
    }
  }
}
