import {Injectable} from '@angular/core';
import {ActivatedRoute, ActivatedRouteSnapshot, Resolve} from '@angular/router';
import {empty, Observable, of} from 'rxjs';
import {catchError, flatMap, map} from 'rxjs/operators';
import {AuthService} from 'src/app/core/services/global/authentication.service';
import {MaskEventService} from 'src/app/core/services/mask-event.service';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';
import {coreUtils} from 'src/app/core/util/coreUtils';

import {FieldExceptionModule} from '../field-exception.module';
import {FormConfig} from '../model/form-config';

@Injectable({providedIn: FieldExceptionModule})
export class FieldExceptionFormResolver implements Resolve<Observable<any>> {
  caseId: string;
  params: any;
  constructor(
      private dataService: DataService, private maskService: MaskEventService,
      private authService: AuthService) {}

  resolve(route: ActivatedRouteSnapshot) {
    this.maskService.mask('maskApp', 'Loading...');
    this.caseId = route.queryParams.caseId;
    this.params = {
      user: this.authService.getUserName(),
      tenant_id: coreUtils.removeHifen(window.location.hostname)
    };
    const formField = new FormConfig();
    return this.dataService.getFieldExceptionForm(this.params, this.caseId)
        .pipe(
            map((resp: any) => {
              this.maskService.unMask('maskApp');
              if (resp.flag) {
                const respData = resp as FormConfig;
                if (resp.corrupted) {
                  formField.corrupted = true;
                  formField.file_name = respData.file_name;
                } else {
                  formField.ocr_data = respData.ocr_data;
                  formField.data = respData.data;
                  formField.dropdown_values = respData.dropdown_values;
                  formField.caseId = this.caseId;
                  formField.table = respData.table;
                  formField.file_name = respData.file_name;
                  formField.highlight = respData.highlight;
                  formField.time_spent = respData.time_spent;
                  formField.timer = respData.timer;
                  formField.failures = respData.failures;
                  formField.template_list = respData.template_list;
                  formField.template_name = respData.template_name;
                  formField.addon_table = respData.addon_table;
                }
              } else {
                formField.filebeingused = true;
              }
              return formField;
            }),
            catchError((err, caught) => {
              this.maskService.unMask('maskApp');
              return of({});
            }));
  }
}
