import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {Subject, timer} from 'rxjs';
import {takeUntil} from 'rxjs/operators';
import {DataService} from 'src/app/core/services/serviceBridge/data.service';
import {coreUtils} from 'src/app/core/util/coreUtils';

@Component({
  selector: 'app-monitor-flips',
  templateUrl: './monitor-flips.component.html',
  styleUrls: ['./monitor-flips.component.scss']
})
export class MonitorFlipsComponent implements OnInit, OnDestroy {
  inputData: any = {};
  flipData: any;
  private killRefershFlips: Subject<void> = new Subject();

  @Input()
  set flipInput(data: any) {
    this.inputData = data;
    if (data.refresh === 'auto') {
      timer(0, 30000)
          .pipe(
              // This kills the request if the user closes the component
              takeUntil(this.killRefershFlips),
              )
          .subscribe(t => {
            this.getFlipData(data.fromDate, data.toDate);
          });
    } else {
      this.getFlipData(data.fromDate, data.toDate);
    }
  }

  @Input()
  set manualRefresh(val: any) {
    if (val) {
      this.getFlipData(this.inputData.fromDate, this.inputData.toDate);
    }
  }

  constructor(
      private dataService: DataService) {}

  ngOnInit() {}

  getFlipData(fDate, tDate) {
    const obj = {
      fromDate: fDate,
      toDate: tDate,
      header: this.inputData.header,
      tenant_id: coreUtils.removeHifen(window.location.hostname),
      flip_side: this.inputData.flipSide ? this.inputData.flipSide : null
    };
    this.dataService.getEachCardDetails(obj, this.inputData.route)
        .subscribe(
            resp => {
              this.flipData = resp.data;
            },
            (error) => {
            });
  }


  convertToArray(val) {
    val = val.toString();
    return val.split('');
  }

  isEmpty(obj) {
    for (const key in obj) {
      if (obj.hasOwnProperty(key)) {
        return false;
      }
    }
    return true;
  }

  ngOnDestroy() {
    this.killRefershFlips.next();
  }
}
