import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

export interface htmltable {
  header: any;
  rowData: any
}

@Component({
  selector: 'app-html-table',
  templateUrl: './html-table.component.html',
  styleUrls: ['./html-table.component.scss']
})
export class HtmlTableComponent implements OnInit {
  data:htmltable;

  @Input()
  set tableData(data: any) {
    this.data = data;
  }

  @Output() outputData : EventEmitter<any> = new EventEmitter<any>();

  ngOnInit() { }

  addRow(tableIndex: number) {
    const newRow:any = {};
    if(tableIndex === 0) {
     this.data.header.forEach(key => {
        newRow[key] = "";
      })
    } else {
      Object.keys(this.data.rowData[tableIndex]).forEach(key => {
        newRow[key] = "";
      })
    }
    this.data.rowData.push(newRow);
  }

  deleteRow(tableIndex: number) {
    this.data.rowData.splice(tableIndex, 1);
  }

  emitValues() {
    this.outputData.emit(this.data)
  }
}
