import { ValidatorFn, FormControl, Validators, ValidationErrors, AbstractControl } from '@angular/forms';

export class customNumberValidators {
    static min(min: number): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
          if (!(control.value)) {
            return null;  
          }
          const value = parseFloat(control.value);
          return !isNaN(value) && value.toString().length < min ? {'min': {'min': min, 'actual': control.value.toString().length}} : null;
        };
      }
    
      static max(max: number): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
          if (!(control.value)) {
            return null;  // don't validate empty values to allow optional controls
          }
          const value = parseFloat(control.value);
          return !isNaN(value) && value.toString().length > max ? {'max': {'max': max, 'actual': control.value.toString().length}} : null;
        };
      }
  }