import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { DataService } from 'src/app/core/services/serviceBridge/data.service';
import { Subject, timer } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { coreUtils } from 'src/app/core/util/coreUtils';

@Component({
  selector: 'app-monitor-text',
  templateUrl: './monitor-text.component.html',
  styleUrls: ['./monitor-text.component.scss']
})
export class MonitorTextComponent implements OnInit, OnDestroy {

  constructor(private dataService: DataService) { }

  inputData: any = {};
  cardData: any = [];
  private killRefershCards: Subject<void> = new Subject();

  @Input()
  set textInputObj(data: any) {
    this.inputData = data;
    if (data.refresh === 'auto') {
      timer(0, 30000)
          .pipe(
              // This kills the request if the user closes the component
              takeUntil(this.killRefershCards),
              )
          .subscribe(t => {
            this.getTextData(data.fromDate, data.toDate);
          });
    } else {
      this.getTextData(data.fromDate, data.toDate);
    }
  }

  @Input()
  set manualRefresh(val: any) {
    if (val) {
      this.getTextData(this.inputData.fromDate, this.inputData.toDate);
    }
  }

  getTextData(fDate, tDate) {
    const obj = {
      fromDate: fDate,
      toDate: tDate,
      header: this.inputData.header,
      tenant_id: coreUtils.removeHifen(window.location.hostname),
      flip_side: this.inputData.flipSide ? this.inputData.flipSide : null
    };
    this.dataService.getEachCardDetails(obj, this.inputData.route)
        .subscribe(
            resp => {
              this.cardData = resp.data;
            },
            (error) => {
            });
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    this.killRefershCards.next();
  }

}
